import React, { Component } from 'react';

class ActivityBar extends Component {
  render() {
    return (
      <div className="activity-bar">
        <ul className="activity-bar__icon-list">
          <li onClick={() => {this.props.switcher('normal')}}><i className="fa fa-circle activity-bar__normal-icon"></i></li>
          <li onClick={() => {this.props.switcher('contrast')}}><i className="fa fa-circle activity-bar__contrast-icon"></i></li>
          <li onClick={() => {this.props.switcher('light')}}><i className="fa fa-circle activity-bar__light-icon"></i></li>
        </ul>
      </div>
    );
  }
}

export default ActivityBar;



